import React from "react";

const About = () => (
    <div className>
        <div className="subpage_header">
            <h2 className="centered_text"> How did Butch get started in the cookie business? </h2>
        </div>
        <p className="centered_text">
        Butch is a guy always up for a challenge.  It started with a personal challenge to try something new, and also give a gift for a friend. 
        </p>
        <p className="centered_text">  Hours and several batches later … success!
        </p>
        <h3 className="centered_text"> The End </h3>
    </div>
);

export default About;



